.container-card {
  width: 1170px;
  height: 368px;
  margin: 10px 10px 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff; 
  box-shadow: 0 3px 13px 1px rgb(0 0 0 / 9%);
}

.img {
  height: 100%;
  width: auto;
}

.container-text {
  text-align: center;
  padding: 40px;
  font-family: Lato, sans-serif;
  font-weight: 400;
  line-height: 1.5rem;
  color: #3b3b4f;
}

.name {
  font-size: 1.3rem;
}

.position {
  font-size: 1.3rem;
  margin-top: 15px;
  padding-bottom: 30px;
}
  
.desc {
  font-size: 1.1rem;
  text-align: justify;
}